import React from 'react';

function BillForm() {
    return (

            <form action="/submit" method="post">
                <h2>Enter a Bill</h2>
                <input type="text" className="form-control" name="bill" placeholder="What bill do you want to add?" />
                <input type="text" className="form-control" name="billValue" placeholder="How much is owed?" />
                <input type="text" className="form-control" name="billDate" placeholder="When is this bill due?" />
                <button className="btn btn-lg btn-primary btn-block" id="submit" type="submit">Submit</button>
            </form>

    );
}

export default BillForm;